import { defineComponent, ref } from 'vue';
import ListComp from '@/components/list/index.vue';
import ClientQrCodeComp from '@/components/client-qr-code/index.vue';
import UserInfoComp from '@/components/user-info/index.vue';
import useHttpRequest from '@/utils/request';
import { ElMessage } from 'element-plus';
import { useRoute } from 'vue-router';
import { routerPush } from '@/utils';
export default defineComponent({
    components: {
        ListComp: ListComp,
        ClientQrCodeComp: ClientQrCodeComp,
        UserInfoComp: UserInfoComp
    },
    setup: function () {
        var route = useRoute();
        // 列表数据获取
        var _a = useHttpRequest(), isLoading = _a.isLoading, adornUrl = _a.adornUrl, httpRequest = _a.httpRequest;
        var userData = ref();
        var getUserData = function () {
            httpRequest({
                url: adornUrl("/api/v1/user/" + route.params.userName),
                method: 'get'
            }).then(function (_a) {
                var data = _a.data;
                userData.value = data.data;
            }).catch(function (e) {
                ElMessage.error('请求失败');
                console.error(e);
            });
        };
        getUserData();
        // 查看详情
        var seeDetail = function (id) {
            routerPush({
                path: "/detail",
                query: {
                    id: id,
                    userName: route.params.userName
                }
            });
        };
        return {
            isLoading: isLoading,
            userData: userData,
            seeDetail: seeDetail,
        };
    },
});
